import React from 'react'
import styled from 'styled-components'
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';

import { colors } from '../../styles'
import bscscan from '../images/bscscan.png'
import chartpoo from '../images/chartpoo.png'
import facebook from '../images/facebook.png'
import dextools from '../images/dextools.png'
import telegram from '../images/telegram.webp'
import medium from '../images/medium.png'
import reddit from '../images/reddit.png'
import twitter from '../images/twitter.png'
import {
    bscscanUrl,
    chartpooUrl,
    facebookUrl,
    dextoolsUrl,
    telegramUrl,
    mediumUrl,
    redditUrl,
    twitterUrl
} from '../../../config/constants/iconUrl'

const InnerContainer = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;
    overflow: auto;
    img {
        cursor: pointer;
        width: 60px;
        height: 60px;
        background-color: white;
        border-radius: 30px;
        &:hover {
            filter: drop-shadow(0 0 10px #0003);
            transition: all 0.2s ease-in-out;
        }
    }
`
const IconContainer = styled.div`
    width: 100%;
    background-color: #F2F2F2;
`
const ImgWrapper = styled.div`
    margin: 2em 0.5em;
    min-width: 60px;
`

const IconList: React.FC = () => {
    const iconData = [
        { id: 1, icon: bscscan, url: bscscanUrl },
        { id: 2, icon: chartpoo, url: chartpooUrl },
        { id: 3, icon: facebook, url: facebookUrl },
        { id: 4, icon: dextools, url: dextoolsUrl },
        { id: 5, icon: telegram, url: telegramUrl },
        { id: 6, icon: medium, url: mediumUrl },
        { id: 7, icon: reddit, url: redditUrl },
        { id: 8, icon: twitter, url: twitterUrl },
    ]
    return (
        <IconContainer>
            <Container maxWidth="lg">
                <InnerContainer>
                    {
                        iconData.map((item) => (
                            <ImgWrapper key={item.id} onClick={() => window.open(item.url, '_blank')} aria-hidden="true">
                                <img src={item.icon} alt="icon" />
                            </ImgWrapper>
                        ))
                    }
                </InnerContainer>
            </Container>
        </IconContainer>
    )
}

export default IconList
