import React from 'react'
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import './dotStyle.css'
import sliderImg1 from '../images/sliderImg1.jpg'
import sliderImg2 from '../images/sliderImg2.jpg'
import sliderImg3 from '../images/sliderImg3.jpg'

const SliderComponent = () => {
    const slickSettings = {
        dots: true,
        infinite: true,
        speed: 2000,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 4000,
    };
    return (
        <div style={{ width: '100%' }}>
            <Slider {...slickSettings}>
                <img src={sliderImg1} alt="slider1" />
                <img src={sliderImg2} alt="slider2" />
                <img src={sliderImg3} alt="slider3" />
            </Slider>
        </div>
    )
}

export default SliderComponent
